.Model {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.6);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 9999;
  }
  
  .Model_box {
    display: flex;
    background: white;
    border-radius: 12px;
    overflow: hidden;
    max-width: 800px;
    width: 100%;
    box-shadow: 0 10px 40px rgba(0, 0, 0, 0.2);
  }
  
  .Model_box_left {
    flex: 1;
    padding: 20px;
    background: #f3f3f3;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .Model_box_right {
    flex: 1;
    padding: 30px;
  }
  
  .Model_box_right h1 {
    font-size: 24px;
    margin-bottom: 10px;
  }
  
  .Model_box_right span {
    color: #0070f3;
  }
  
  .Model_box_right {
    flex: 1;
    padding: 30px;
    color: #000; /* ✅ Set text color to black */
  }
  
  
  .Model_box_right small {
    display: block;
    margin-bottom: 20px;
    color: gray;
  }
  
  .Model_box_right_info {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
  }
  
  .Model_box_right_info input {
    margin-left: 10px;
    padding: 8px 12px;
    width: 100%;
    border: 1px solid black;
    border-radius: 6px;
  }
  
  .Model_box_right_name_btn {
    display: flex;
    gap: 10px;
  }
  
  .Model_box_right_name_btn button {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    background-color: #0070f3;
    color: white;
    border: none;
    padding: 10px 12px;
    border-radius: 6px;
    cursor: pointer;
    transition: background 0.2s ease-in-out;
  }
  
  .Model_box_right_name_btn button:hover {
    background-color: #005bb5;
  }
  