.Error {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    background-color: #fff0f0;
    border-top: 1px solid #ffcccc;
  }
  
  .Error_box {
    background-color: #ffe6e6;
    border: 1px solid #ff4d4f;
    padding: 16px 24px;
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(255, 77, 79, 0.1);
    color: #a8071a;
    font-weight: 600;
    text-align: center;
  }
  