/* Filter.module.css */

/* ---------- wrapper ---------- */
.Filter {
    width: 100%;
    padding: 0.75rem 1.25rem;
    background: #121212;                /* card-like bar */
    border-bottom: 1px solid #1f1f1f;
  }
  
  /* main row container */
  .Fitler_box {
    max-width: 950px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 1rem;
  }
  
  /* ---------- left side (search) ---------- */
  .Fitler_box_left {
    flex: 1;
    display: flex;
    align-items: center;
    gap: 0.6rem;
    background: #1b1b1b;
    padding: 0.5rem 0.75rem;
    border-radius: 0.55rem;
  }
  
  .Fitler_box_left input {
    flex: 1;
    background: transparent;
    border: none;
    outline: none;
    color: #e5e5e5;
    font-size: 0.9rem;
  }
  
  .Fitler_box_left input::placeholder {
    color: #7a7a7a;
  }
  
  /* ---------- right side (buttons) ---------- */
  .Fitler_box_right {
    display: flex;
    align-items: center;
    gap: 0.75rem;
  }
  
  .Fitler_box_right button {
    display: inline-flex;
    align-items: center;
    gap: 0.45rem;
    padding: 0.45rem 0.9rem;
    font-size: 0.8rem;
    font-weight: 500;
    border: none;
    border-radius: 0.5rem;
    cursor: pointer;
    transition: background 0.18s ease;
    color: #fff;
  }
  
  .Fitler_box_right button:nth-child(1) {
    background: #b91c1c;                /* red for "Clear chat" */
  }
  
  .Fitler_box_right button:nth-child(1):hover {
    background: #991b1b;
  }
  
  .Fitler_box_right button:nth-child(2) {
    background: #2563eb;                /* blue for "Add friend" */
  }
  
  .Fitler_box_right button:nth-child(2):hover {
    background: #1e4ed8;
  }
  
  /* ---------- modal overlay ---------- */
  .Filter_model {
    position: fixed;
    inset: 0;
    background: rgba(0, 0, 0, 0.65);
    display: flex;
    align-items: center;
    justify-content: center;
    backdrop-filter: blur(2px);
    z-index: 1000;
  }
  
  /* ---------- responsive tweaks ---------- */
  @media (max-width: 540px) {
    .Fitler_box {
      flex-direction: column;
      align-items: stretch;
    }
  
    .Fitler_box_right {
      justify-content: space-between;
    }
  
    .Fitler_box_right button {
      flex: 1;
      justify-content: center;
    }
  }
  