.Card {
    padding: 10px;
    margin: 10px 0;
    border-radius: 10px;
    background-color: #f8f8f8;
    cursor: pointer;
    transition: box-shadow 0.2s ease-in-out;
    border: 1px solid #ddd;
  }
  
  .Card:hover {
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  }
  
  .Card_box {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .Card_box_left {
    flex: 0 0 auto;
  }
  
  .Card_box_left_img {
    border-radius: 50%;
  }
  
  .Card_box_right {
    flex: 1;
    margin-left: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .Card_box_right_middle h4 {
    margin: 0;
    font-size: 18px;
    color: #333;
  }
  
  .Card_box_right_middle small {
    color: #777;
  }
  
  .Card_box_right_end small {
    font-weight: bold;
    color: #aaa;
  }
  